import axios from 'axios';

export const AddMixnMatch = (Data) =>
{
    return new Promise((resolve, reject) =>
    {
        axios.post('/api/mixnmatch',Data)
            .then(res => {
                resolve(res);
            }).catch(err=>{
                reject(err);
            });
    });
};


export const UpdateMixnMatch = (Data) =>
{
    return new Promise((resolve, reject) =>
    {
        axios.put('/api/mixnmatch',Data)
            .then(res => {
                resolve(res);
            }).catch(err=>{
                reject(err);
            });
    });
};


export const GetMixnMatchById = (id) =>
{
    return new Promise((resolve, reject) =>
    {
        axios.get(`/api/mixnmatch?id=${id}`)
            .then(res => {
                resolve(res);
            }).catch(err=>{
                reject(err);
            });
    });
};

export const GetMixnMatchList = () =>
{
    return new Promise((resolve, reject) =>
    {
        axios.get('/api/mixnmatch/list')
            .then(res => {
                resolve(res);
            }).catch(err=>{
                reject(err);
            });
    });
};


export const GetAllMixnMatchDropDown = () =>{
    return new Promise((resolve, reject) => {
    
        GetMixnMatchList().then(res=>
        {
            const dropdownSet = [];
            res.data.result.forEach(r => 
            {
                dropdownSet.push({
                    value: r.mixNmatchId, 
                    label: r.priceGroupId + ' --- ' + r.description
                });
            });
            resolve(dropdownSet);
        }).catch((err)=>{
            reject(err);
        }); 
    });    
};


export const RemoveMixnMatch = (id) =>
{
    return new Promise((resolve, reject) =>
    {
        axios.delete(`/api/mixnmatch/${id}`)
            .then(res => {
                resolve(res);
            }).catch(err=>{
                reject(err);
            });
    });
};
