/* eslint-disable no-prototype-builtins */
/* eslint-disable no-unused-vars */
import React, { useState } from 'react';
import { useEffect } from 'react';
import Select from 'react-select';
import TextField from '@mui/material/TextField';
import * as DepartmentAPI from 'Services/Department';
import * as TaxRateAPI from 'Services/TaxRate';
import * as MixNMatchAPI from 'Services/MixNMatch';
import CurrencyInput from 'react-currency-input-field';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { AiOutlineClose } from 'react-icons/ai';
import ProductList from 'Components/Utility/ProductList';
import swal from 'sweetalert2';
import { NodataIndicator } from 'Components/Utility';
import { currencyFormat, CusDateFormater } from 'Helpers/StringHelpers';
import CustomSelect from 'Components/Utility/CustomSelect';
import { MixAndMatchInit } from 'Model/MixAndMatch.model';
import DefaultConfig from 'Config/GlobalVariable';
import { MixandMatchSchema, DicountlvlSchema } from 'Validation';


export const MixandMatch = () => {

    const PriceGroupType = [{ value: 1, label: 'Discount Amount' }, { value: 2, label: 'Bulk Price' }];
    const Dicountlvlinit = { amount: 0, qty: 0 };
    const [Departmentlist, setDepartmentlist] = useState([]);
    const [TaxRate, setTaxRate] = useState([]);
    const [MixAndMatchlist, setMixAndMatchlist] = useState([]);
    const [MixAndMatchData, setMixAndMatchData] = useState(MixAndMatchInit);
    const [DicountlvlData, setDicountlvlData] = useState(Dicountlvlinit);
    const [NewProductModel, setNewProductModel] = useState(false);
    
    const [PopupDiscountLevel, setPopupDiscountLevel] = useState(false);
    const [IteminGroup, setIteminGroup] = useState([]);
    const [DicountlvlList, setDicountlvlList] = useState([]);
    const [SelectVal, setSelectVal] = useState([]);
    const [EditMode, setEditMode] = useState(false);

    const GetAllDepartment = () => DepartmentAPI.DepartmentDropDownList().then(res => { setDepartmentlist(res); }).catch(() => { });
    const GetAllTaxRate = () => TaxRateAPI.GetAlltaxList().then(res => { setTaxRate(res[0]); }).catch(() => { });
    const GetMixNMatch = (id) => MixNMatchAPI.GetMixnMatchById(id).then(res => {
        setMixAndMatchData(res.data.result);
        setIteminGroup(res.data.result.mnMProducts);
        setDicountlvlList(res.data.result.discountLevels);
    }).catch(() => { });
    const GetMixAndMatchlist = () => MixNMatchAPI.GetAllMixnMatchDropDown().then(res => { setMixAndMatchlist(res); }).catch(() => { });

    const handleChange = (name, val) => setMixAndMatchData({ ...MixAndMatchData, [name]: val });
    const handleDicountlvlChange = (name, val) => setDicountlvlData({ ...DicountlvlData, [name]: val });
    const handleModelClose = () => { setNewProductModel(false); };
    const handleModelDiscountLevelClose = () => { setPopupDiscountLevel(false); };

    useEffect(() => {
        GetAllDepartment();
        GetAllTaxRate();
        GetMixAndMatchlist();
    }, []);

    const DeleteChoiceItems = (index) => {
        swal.fire({
            title: 'Are you sure you want to delete?',
            showCancelButton: true,
            confirmButtonText: 'Yes',
            cancelButtonText: 'No',
            ...DefaultConfig.SwalGlobalwithConfirm
        }).then((result) => {
            if (result.isConfirmed) {
                const rows = [...IteminGroup];
                rows.splice(index, 1);
                setIteminGroup(rows);
            }
            else if (result.isDenied) {
                swal.close();
            }
        });
    };

    const DeleteDiscountlvl = (index) => {
        swal.fire({
            title: 'Are you sure you want to delete?',
            showCancelButton: true,
            confirmButtonText: 'Yes',
            cancelButtonText: 'No',
            ...DefaultConfig.SwalGlobalwithConfirm
        }).then((result) => {
            if (result.isConfirmed) {
                const rows = [...DicountlvlList];
                rows.splice(index, 1);
                setDicountlvlList(rows);
                
            }
            else if (result.isDenied) {
                swal.close();
            }
        });
    };

    const GetSelectedRow = (row) => {
        if (IteminGroup.filter(x => x.productId === row.productId).length > 0) {
            swal.fire({
                title: 'Product already added!',
                ...DefaultConfig.SwalGlobal
            });
        } else {
            const rows = [...IteminGroup, row];
            setIteminGroup(rows);
            setNewProductModel(false);
        }
    };

    const AddDicountlvl = () => {

        const GetRow = DicountlvlList.filter(x=>x.qty === DicountlvlData.qty);
        if(GetRow.length){
            swal.fire({
                title: 'Qty already added!',
                ...DefaultConfig.SwalGlobal
            });
            return;
        }
        if(DicountlvlData.qty === 0){
            swal.fire({
                title: 'Atleast 1 quantity is required!',
                ...DefaultConfig.SwalGlobal
            });
            return;
        }

        DicountlvlSchema().validate(DicountlvlData, { abortEarly: false })
            .then(function (_e) {

                const rows = [...DicountlvlList, DicountlvlData];
                setDicountlvlList(rows);
                setPopupDiscountLevel(false);

            }).catch((err) => {
                // eslint-disable-next-line no-unused-vars
                let error = '';
                err.inner.forEach(e => {
                    error += e.message + '<br />';
                });
                swal.fire({ title: err.inner[0].message, ...DefaultConfig.SwalGlobal });
            });


    };

    const CleanData = () => {
        setMixAndMatchData(MixAndMatchInit);
        setIteminGroup([]);
        setDicountlvlList([]);
        setSelectVal([]);
        setEditMode(false);
    };

    const AddMixAndMatch = () => {
        let newObj = Object.assign({}, MixAndMatchData);
        newObj.mnMProducts = IteminGroup.map(function (obj) { return { productId: obj.productId }; });
        newObj.discountLevels = DicountlvlList.map(function (obj) { return obj; });

        MixandMatchSchema(MixAndMatchData).validate(MixAndMatchData, { abortEarly: false })
            // eslint-disable-next-line no-unused-vars
            .then(function (_e) {
                if(!newObj.discountLevels.length && newObj.priceGroupTypeId === 2){
                    swal.fire({
                        title: 'Atleast 1 quantity is required for discount level!',
                        ...DefaultConfig.SwalGlobal
                    });
                    return;
                }
                // alert('perfect');
                if (newObj.mixNmatchId > 0) {
                    swal.showLoading();
                    MixNMatchAPI.UpdateMixnMatch(newObj).then(res => {
                        
                        swal.fire({
                            title: 'Price Group updated successfully.',
                            ...DefaultConfig.SwalGlobal
                        });
                        GetMixNMatch(res.data.result.mixNmatchId);
                    }).catch(err => {
                        swal.fire({
                            title: 'Error',
                            ...DefaultConfig.SwalGlobal
                        });
                    });
                }
                else {
                    swal.showLoading();
                    MixNMatchAPI.AddMixnMatch(newObj).then(res => {
                        swal.fire({
                            title: 'Price Group added successfully.',
                            ...DefaultConfig.SwalGlobal
                        });
                        GetMixNMatch(res.data.result.mixNmatchId);
                        GetMixAndMatchlist();
                    }).catch(err => {
                        if (err.response.data.errors.hasOwnProperty('PriceGroupId')) {
                            swal.fire({ title: err.response.data.errors.PriceGroupId[0], ...DefaultConfig.SwalGlobal });
                        }else {
                            swal.fire({
                                title: 'Error',
                                ...DefaultConfig.SwalGlobal
                            });
                        }
                    });
                }
                // eslint-disable-next-line no-unused-vars
            }).catch((err) => {
                // eslint-disable-next-line no-unused-vars
                swal.fire({ title: err.inner[0].message, ...DefaultConfig.SwalGlobal });
            });
    };

    const RemoveMixnMatch = () => {
        swal.fire({
            title: 'Are you sure want to delete this record permanently,<br> It will remove all dependent data?',
            showCancelButton: true,
            confirmButtonText: 'Yes',
            cancelButtonText: 'No',
            preConfirm: () => {
                swal.showLoading();
                swal.getTitle().hidden = true;
                swal.getCancelButton().hidden = true;
                let newObj = Object.assign({}, MixAndMatchData);
               return MixNMatchAPI.RemoveMixnMatch(newObj.mixNmatchId).then(res => {
                    CleanData();
                    GetMixAndMatchlist();
                    swal.fire({
                        title: 'Mix N Match delete!',
                        ...DefaultConfig.SwalGlobal
                    });
                    return true;
                }).catch(err => {
                    swal.fire({ title: err.inner[0].message, ...DefaultConfig.SwalGlobal });
                    return true;
                });
            },
            ...DefaultConfig.SwalGlobalwithConfirm
        });
    };

    return (
        <div id='pos_view' className='mixandmatch-page'>
            <div className='depart-main-view'>
                <div className='top-filter-row'>
                    <div className='lt-title-bx'>
                        <h2>{!EditMode ? 'Add ' : 'Edit '}Mix and Match</h2>
                    </div>
                    <div className='rt-filter-bx filter-hd'>
                        <div className='select-20'>
                        <CustomSelect className='select-cm' options={MixAndMatchlist}
                            value={MixAndMatchlist.filter(({ value }) => SelectVal.includes(value))}
                            onChange={(e) => {
                                if (e === null) {
                                    CleanData();
                                }
                                else {
                                    GetMixNMatch(e.value);
                                    setSelectVal([e.value]);
                                    setEditMode(true);
                                }
                            }}
                            placeholder="Mix N Match" />
                        </div>
                        <div className='row searchbar-mainview'>
                            <div className='icons-list'>
                                <button className='icons-hd-cm plus-icons'
                                    disabled={MixAndMatchData.mixNmatchId === 0}
                                    onClick={() => {
                                        CleanData();
                                    }}></button>
                                <button className='icons-hd-cm checks-icons' onClick={() => {
                                    AddMixAndMatch();
                                }}></button>
                                <button className="icons-hd-cm delete-icons"
                                    disabled={MixAndMatchData.mixNmatchId === 0}
                                    onClick={() => {
                                        //  handleRemoveVendor();                                        
                                        RemoveMixnMatch();
                                    }}></button>
                            </div>
                        </div>
                    </div>
                </div>
                <hr className='full-width' />
                <div className='margin-depart table-cm mixandmatch-page'>
                    <div className='row'>
                        <div className='col-md-12 standard-left'>
                            <div className='row min-row'>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='Department' className='col-sm-4 col-form-label'>Department</label>
                                        <div className='col-md-8'>
                                            <Select options={Departmentlist}
                                                value={Departmentlist.filter(({ value }) => value === MixAndMatchData.departmentId)}
                                                onChange={(e) => {
                                                    e === null ? handleChange('departmentId', null) : handleChange('departmentId', e.value);
                                                }}
                                                className='product-depatment' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='productCode' className='col-sm-4 col-form-label'>Price Group ID<span>*</span></label>
                                        <div className='col-md-8'>
                                            <input type='text'
                                                name='priceGroupId'
                                                placeholder='Price Group ID'
                                                disabled={MixAndMatchData.mixNmatchId > 0}
                                                value={MixAndMatchData.priceGroupId}
                                                onChange={(e) => { handleChange(e.target.name, e.target.value); }}
                                                className='form-control'
                                                id='priceGroupId' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='productCode' className='col-sm-4 col-form-label'>Description<span>*</span></label>
                                        <div className='col-md-8'>
                                            <input type='text' name='description'
                                                placeholder='Description'
                                                value={MixAndMatchData.description}
                                                onChange={(e) => { handleChange(e.target.name, e.target.value); }}
                                                className='form-control' id='description' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='Tax' className='col-sm-4 col-form-label'>Price Group Type</label>
                                        <div className='col-md-8'>
                                            <Select options={PriceGroupType}
                                                value={PriceGroupType.filter(({ value }) => value === MixAndMatchData.priceGroupTypeId)}
                                                onChange={(e) => {
                                                    e === null ? handleChange('priceGroupTypeId', null) : handleChange('priceGroupTypeId', e.value);
                                                }}
                                                className='product-depatment' />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='productCode' className='col-sm-4 col-form-label'>Quantity Required<span>*</span></label>
                                        <div className='col-md-8'>

                                            <CurrencyInput
                                                className='form-control'
                                                name="qtyRequired"
                                                id="qtyRequired"
                                                data-number-to-fixed="5"
                                                data-number-stepfactor="100"
                                                value={MixAndMatchData.qtyRequired}
                                                placeholder=""
                                                onValueChange={(e) => {
                                                    //SetProductInfo({ ...ProductInfo, cost: e });

                                                    handleChange('qtyRequired', e);
                                                }}
                                                allowDecimals
                                                decimalsLimit="5"
                                                disableAbbreviations
                                                disabled={MixAndMatchData.priceGroupTypeId !== 1}
                                            />

                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='productCode' className='col-sm-4 col-form-label'>Amount to Discount<span>*</span></label>
                                        <div className='col-md-8'>

                                            <CurrencyInput
                                                className='form-control'
                                                prefix='$'
                                                name="cost"
                                                id="cost"
                                                data-number-to-fixed="5"
                                                data-number-stepfactor="100"
                                                value={MixAndMatchData.amountToDiscount}
                                                placeholder=""
                                                onValueChange={(e) => {
                                                    handleChange('amountToDiscount', e);
                                                }}
                                                allowDecimals
                                                decimalsLimit="5"
                                                disableAbbreviations
                                                disabled={MixAndMatchData.priceGroupTypeId !== 1}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='Tax' className='col-sm-4 col-form-label'>Tax</label>
                                        <div className='col-md-8'>

                                            <Select
                                                isClearable='false'
                                                isMulti
                                                value={TaxRate.filter(({ value }) => MixAndMatchData.taxList.includes(value))}
                                                options={TaxRate}
                                                onChange={(e) => {
                                                    var arr = [];
                                                    e.map(x => arr.push(x.value));
                                                    setMixAndMatchData({ ...MixAndMatchData, taxList: arr });
                                                }}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='productCode' className='col-sm-4 col-form-label'>Start Date</label>
                                        <div className='col-md-8'>
                                            <TextField
                                                className='data-picker-field data-picker-full'
                                                id="datetime-local"
                                                value={CusDateFormater(MixAndMatchData.startDate)}
                                                type="date"
                                                sx={{
                                                    '& legend': { display: 'none' },
                                                    '& fieldset': { top: 0 },
                                                }}
                                                onChange={(e) => {
                                                    handleChange('startDate', e.target.value);
                                                }}
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className='form-group col-md-4'>
                                    <div className='row'>
                                        <label htmlFor='productCode' className='col-sm-4 col-form-label'>End Date</label>
                                        <div className='col-md-8'>
                                            <TextField
                                                className='data-picker-field data-picker-full'
                                                id="datetime-local"
                                                value={CusDateFormater(MixAndMatchData.endDate)}
                                                type="date"
                                                sx={{
                                                    '& legend': { display: 'none' },
                                                    '& fieldset': { top: 0 },
                                                }}
                                                onChange={(e) => {
                                                    handleChange('endDate', e.target.value);
                                                }}
                                            />
                                        </div>
                                    </div>
                                </div>

                                <div className='form-group col-md-4'>
                                    <div className="info-bx">
                                        <div className="form-check">
                                            <label className="form-check-label">Apply discounts only<br></br> once per invoice</label>
                                            <input className="form-check-input"
                                                onChange={(e) => { handleChange('isPerInvoice', e.target.checked); }}
                                                name='idProofRequired1'
                                                checked={MixAndMatchData.isPerInvoice}
                                                type="checkbox"></input>
                                        </div>
                                    </div>
                                </div>

                                {!MixAndMatchData.isPerInvoice ? (
                                    <>
                                        <div className='form-group col-md-4'>
                                            <div className='row'>
                                                <label htmlFor='productCode' className='col-sm-4 col-form-label'>Customer Days<span>*</span></label>
                                                <div className='col-md-8'>
                                                    <CurrencyInput
                                                        className='form-control'
                                                        name="customerDays"
                                                        id="customerDays"
                                                        data-number-to-fixed="5"
                                                        data-number-stepfactor="100"
                                                        value={MixAndMatchData.customerDays}
                                                        placeholder=""
                                                        onValueChange={(e) => {
                                                            handleChange('customerDays', e);
                                                        }}
                                                        allowDecimals
                                                        decimalsLimit="5"
                                                        disableAbbreviations
                                                    />

                                                </div>
                                            </div>
                                        </div>

                                        <div className='form-group col-md-4'>
                                            <div className='row'>
                                                <label htmlFor='productCode' className='col-sm-4 col-form-label'>Customer Qty<span>*</span></label>
                                                <div className='col-md-8'>
                                                    <CurrencyInput
                                                        className='form-control'
                                                        name="customerLimit"
                                                        id="customerLimit"
                                                        data-number-to-fixed="5"
                                                        data-number-stepfactor="100"
                                                        value={MixAndMatchData.customerLimit}
                                                        placeholder=""
                                                        onValueChange={(e) => {
                                                            //SetProductInfo({ ...ProductInfo, cost: e });
                                                            handleChange('customerLimit', e);
                                                        }}
                                                        allowDecimals
                                                        decimalsLimit="5"
                                                        disableAbbreviations
                                                    />
                                                </div>
                                            </div>
                                        </div></>
                                ) : (<></>)
                                }


                            </div>
                            <div className='row min-row'>
                                <div className='col-md-12'>
                                    <div className='row'>
                                        <div className='col-md-7 item-in-group-table'>
                                            <div className='btn-mixand-bx'>
                                                <span className="title-tab">Product In Group</span>
                                                <button className="save-btn btn btn-save" onClick={() => {
                                                    setNewProductModel(true);
                                                }}>Add Item</button>
                                            </div>
                                            <div className='tabs-cm'>
                                                <div className='table-sm table-cm'>
                                                    <div className='react-bootstrap-table'>
                                                        <table className='table table-bordered'>
                                                            <thead>
                                                                <tr>
                                                                    <th>Product barcode</th>
                                                                    <th>Product description</th>
                                                                    <th>Price</th>
                                                                    <th>ACTION</th>
                                                                </tr>
                                                            </thead>
                                                            <tbody>
                                                                {IteminGroup.length ?
                                                                    (IteminGroup.map((item, index) => {
                                                                        return (
                                                                            <tr key={index}>

                                                                                <td>{item.productCode}</td>
                                                                                <td>{item.productName}</td>
                                                                                <td>{currencyFormat(item.price)}</td>
                                                                                <td>
                                                                                    <div className='action-btn'>
                                                                                        <button className='delete-icons' onClick={() => {
                                                                                            DeleteChoiceItems(index);
                                                                                        }}></button>
                                                                                    </div>
                                                                                </td>
                                                                            </tr>
                                                                        );
                                                                    })) : (
                                                                        <tr>
                                                                            <td colSpan="4" style={{ textAlign: 'center' }}><NodataIndicator text='No Item' /></td>
                                                                        </tr>
                                                                    )
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                        <div className={'col-md-5 ' + (MixAndMatchData.priceGroupTypeId === 2 ? '' : 'Discount-Levels-Disable')}>
                                            <div className='btn-mixand-bx'>
                                                <span className="title-tab">Discount Levels</span>
                                                <button className="save-btn btn btn-save"
                                                    disabled={MixAndMatchData.priceGroupTypeId !== 2}
                                                    onClick={() => { setPopupDiscountLevel(true); }}
                                                >Add Discount Level</button>
                                            </div>
                                            <div className='tabs-cm'>
                                                <div className='table-sm table-cm'>
                                                    <div className='react-bootstrap-table'>
                                                        <table className='table table-bordered'>
                                                            <thead>
                                                                <tr>
                                                                    <th>Quantity</th>
                                                                    <th>Amount</th>
                                                                    <th>ACTION</th>
                                                                </tr>
                                                            </thead>

                                                            <tbody>
                                                                {DicountlvlList.length ?
                                                                    (DicountlvlList.map((item, index) => {
                                                                        return (
                                                                            <tr key={index}>
                                                                                <td>{item.qty}</td>
                                                                                <td>{currencyFormat(item.amount)}</td>
                                                                                <td>
                                                                                    <div className='action-btn'>
                                                                                        <button className='delete-icons' onClick={() => {
                                                                                            DeleteDiscountlvl(index);
                                                                                        }}></button>
                                                                                    </div>
                                                                                </td>
                                                                            </tr>
                                                                        );
                                                                    })) : (
                                                                        <tr>
                                                                            <td colSpan="3" style={{ textAlign: 'center' }}><NodataIndicator text='No Item' /></td>
                                                                        </tr>
                                                                    )
                                                                }
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <Modal show={NewProductModel} className="inventory-popup select-products-popup cm-popup modal-lg inventory-fix-popup">
                <Modal.Header>
                    <Modal.Title><span>Select Products To Add</span><Button variant="closepopup" onClick={handleModelClose}><AiOutlineClose style={{ fontSize: '1.3020833333333333vw' }} /></Button></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="popup-main-view-row">
                        <div className='depart-main-view  table-cm'>
                            <ProductList SelectedRow={GetSelectedRow} />
                        </div>
                    </div>
                </Modal.Body>
            </Modal>

            <Modal show={PopupDiscountLevel} onShow={() => {
                setDicountlvlData(Dicountlvlinit);
            }} className="cm-popup table-cm modal-lg">
                <Modal.Header>
                    <Modal.Title><span>Add Discount Level</span><Button variant="closepopup" onClick={handleModelDiscountLevelClose}><AiOutlineClose style={{ fontSize: '1.3020833333333333vw' }} /></Button></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div className="container">
                        <div className="margin-popup" />

                        <div className='row mb-0'>
                        <div className="form-group col-md-6">
                                <div className='row'>
                                    <label className="col-sm-5 col-form-label">QTY<span className='required'>*</span></label>
                                    <div className="col-md-7">

                                        <CurrencyInput
                                            className='form-control'
                                            name="qty"
                                            id="qty"
                                            value={DicountlvlData.qty}
                                            defaultValue={0}
                                            placeholder=""
                                            onValueChange={(e) => {
                                                handleDicountlvlChange('qty', e);
                                            }}
                                            allowDecimals
                                            decimalsLimit="2"
                                        />

                                        {/* <input type="text" name='qty'
                                            value={DicountlvlData.qty}
                                            onChange={(e) => { handleDicountlvlChange('qty', e.target.value); }}
                                            className="form-control key-board" /> */}
                                    </div>
                                </div>
                            </div>
                            <div className="form-group col-md-6">
                                <div className='row'>
                                    <label className="col-sm-5 col-form-label">Amount<span className='required'>*</span></label>
                                    <div className="col-md-7">
                                        <CurrencyInput
                                            className='form-control'
                                            prefix='$'
                                            name="amount"
                                            id="amount"
                                            value={DicountlvlData.amount}
                                            defaultValue={0}
                                            placeholder=""
                                            onValueChange={(e) => {
                                                handleDicountlvlChange('amount', e);
                                            }}
                                            allowDecimals
                                            decimalsLimit="2"
                                        />
                                    </div>
                                </div>
                            </div>
                           
                        </div>
                        <div className="margin-popup" />
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button className="save-btn" variant="save" onClick={() => {
                        AddDicountlvl();
                    }}>Save</Button>
                </Modal.Footer>
            </Modal>

            

        </div>
    );
};

export default MixandMatch;